* {
  font-family: "Raleway", sans-serif;
}
.nav {
  margin-left: auto;
}
#navbar-brand {
  font-weight: bold;
  color: coral;
}
ul {
  line-height: 60px;
  overflow: hidden;
}
ul li a {
  padding: 15px 15px;
  text-decoration: none;
  letter-spacing: 2px;
  color: #333;
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
  font-weight: 600;
}
ul li:hover a {
  text-decoration: none;
  color: brown;
}
#root {
  margin-top: 60px;
}
#about {
  background: url(/currencyExchange/static/media/christine-roy-343235-unsplash.31a8c0cd.jpg) center
    center no-repeat;
  background-size: cover;
}
section {
  padding: 7rem 0;
}
.service-box .text-primary {
  color: coral !important;
}

.custom-select {
  width: 100px;
}
#sonuc {
  margin-left: 60px;
  font-size: 20px;
}
#render {
  display: block;
}

#çerçeve {
  background-color: rgba(255, 127, 80, 0.3);
}
.içerik_react {
  padding: 20px 20px 20px 20px;
}

